.footer{
    position: fixed;
    display: flex;
    justify-content: space-between;
    height: 65px;
    width: 98%;
    bottom: 0;
    background-color: #282828;
    padding: 20px;
}

.footer_left{
    flex: 0.3;
    color:white;
    display: flex;
    align-items: center;
    max-width: 300px;
}

.footer_center{
    flex: 0.4;
    color: white;
    padding: 0px 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 300px;
}
.footer_right{
    flex : 0.3;
    color: white;
    display: flex;
    padding: 0px 100px;
    align-items: center;
    justify-content: space-between;
    max-width: 300px;

}

.footer_right * .MuiSlider-root{ color: green;}

.Album_logo{
    height: 60px;
    width: 60px;
    margin-right: 20px;
    object-fit: contain;
}
.footer_green{
    color: #1ed15e;
}

.footer_Icon :hover,.footer_green:hover{
    transition: transform 0.2s ease-in-out;
    transform: scale(1.2) !important;
}
.footer_singer_info>h4{
    margin-bottom: 5px;
    max-width: 300px;
}

.footer_singer_info >p{
    font-size: 12px;
}